/* MarraigePage.css */

/* Style for the container of videos */
.video-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  /* Style for each video element */
  .video-container video {
    width: 400px; /* Set the width of each video */
    height: auto; /* Maintain aspect ratio */
    margin: 10px; /* Add some spacing between videos */
  }
  